.individual {
  overflow: hidden;
  padding: 0px 30px 105px 50px;
}

.individual__title {
  margin-bottom: 42px;
}

.individual__row {
  display: flex;
  align-items: stretch;
  justify-content: space-between;
}

.individual__img {
  flex-grow: 1;
  display: block;
  position: relative;
}

.individual__img img {
  position: absolute;
  top: 50%;
  left: 53%;
  transform: translate(-50%, -50%);
  object-fit: cover;
  width: 180%;
  max-width: none !important;
}

.individual__list {
  flex-shrink: 0;
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 37px;
  position: relative;
  z-index: 2;
}

@media screen and (max-width: 1080px) {
  .individual {
    padding: 0px 0px 80px;
  }
}

@media screen and (max-width: 930px) {
  .individual__row {
    align-items: center;
    flex-direction: column;
  }
  .individual__list {
    grid-template-columns: repeat(3, 1fr);
    column-gap: 20px;
    justify-items: center;
    width: 100%;
  }
  .individual__img {
    min-height: 300px;
    width: 100%;
  }
  .individual__img img {
    width: 120%;
    left: 50%;
  }
}

@media screen and (max-width: 600px) {
  .individual__list {
    display: flex;
    align-items: baseline;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  .individual__img img {
    width: 140%;
  }
}

@media screen and (max-width: 500px) {
  .individual__img img {
    width: 180%;
  }
}
