@import "./variables";

body, html {
    overflow-x: hidden;
    background-color: $body_bg; }

body {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    padding: 50px 10px 0px;
    background-size: 100% auto;
    background-image: url(../img/bg-pattern.png);
    background-repeat: repeat-y; }

@media screen and ( max-width: 720px ) {
    body {
        padding-top: 30px; } }
