@import "./variables";

.contacts {
    padding: 0px 100px;
    &__title {
        margin-bottom: 40px; }
    &__list {
        $gap: 50px;
        display: grid;
        grid-template-columns: 410px 1fr 1fr;
        list-style-type: none;
        column-gap: $gap;
        row-gap: $gap; } }

@media screen and ( max-width: 1210px ) {
    .contacts {
        padding: 0px 50px; } }

@media screen and ( max-width: 1080px ) {
    .contacts {
        padding: 0px 30px;
        &__list {
            $gap: 30px;
            column-gap: $gap;
            row-gap: $gap;
            grid-template-columns: 300px 1fr 1fr; } } }

@media screen and ( max-width: 840px ) {
    .contacts {
        padding: 0px 15px;
        &__list {
            $gap: 20px;
            column-gap: $gap;
            row-gap: $gap + 10px;
            grid-template-columns: 260px 1fr; } } }

@media screen and ( max-width: 530px ) {
    .contacts {
        padding: 0px;
        &__list {
            grid-template-columns: 1fr; }
        &__item {
            &:nth-child(1) {
                order: 2; }
            &:nth-child(2) {
                order: 0; }
            &:nth-child(3) {
                order: 1; } } } }
