.contact {
  background-repeat: no-repeat;
  background-size: 30px auto;
  padding-left: 50px;
  background-position: 0 0;
  position: relative;
}

.contact__text {
  display: block;
  width: 100%;
  font-size: 24px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  color: #ffffff;
  line-height: 130%;
}

.contact__text + .contact__text {
  margin-top: 5px;
}

.contact__text_nowrap {
  white-space: nowrap;
}

.contact__text_small {
  font-size: 18px;
}

.contact__button {
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
}

.contact_map {
  background-image: url(../img/map-icon.png);
}

.contact_phone {
  background-image: url(../img/phone-icon.png);
}

.contact_mail {
  background-image: url(../img/mail-icon.png);
}

@media screen and (max-width: 900px) {
  .contact {
    padding-left: 35px;
    background-size: 25px auto;
    background-position: 0 5px;
  }
  .contact__text {
    font-size: 18px;
  }
  .contact__text_small {
    font-size: 16px;
  }
}
