.popup {
  position: fixed;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.4);
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  transition: all 0.3s ease-out 0s;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
}

.popup__body {
  position: relative;
  background-color: #262626;
  box-shadow: 0 0 10px 4px rgba(0, 0, 0, 0.6);
  padding: 60px 20px 50px;
  border-radius: 10px;
  overflow: hidden;
  width: 100%;
  max-width: 500px;
  transition: all 0.3s ease-out 0s;
}

.popup__close {
  position: absolute;
  right: 10px;
  top: 10px;
  height: 40px;
  width: 40px;
}

.popup__close::before, .popup__close::after {
  content: '';
  display: inline-block;
  height: 2px;
  width: 25px;
  background-color: #fff;
  position: absolute;
  top: 50%;
  left: 50%;
}

.popup__close::before {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.popup__close::after {
  transform: translate(-50%, -50%) rotate(45deg);
}

.popup__title, .popup__subtitle {
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  color: #ffffff;
  text-align: center;
  display: block;
}

.popup__title {
  font-size: 32px;
  margin-bottom: 25px;
}

.popup__subtitle {
  font-size: 20px;
}

.popup__title + .popup__subtitle {
  margin-top: -10px;
  margin-bottom: 45px;
}

.popup__contact {
  margin-bottom: 10px;
}

.popup__form > * + * {
  margin-top: 20px;
}

.popup__return {
  display: block;
  width: 100%;
  max-width: 350px;
  margin: 0 auto;
}

.popup:not(.active) {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.popup:not(.active) .popup__body {
  transform: translateY(-40px);
  opacity: 0;
}

@media screen and (max-width: 500px) {
  .popup__title {
    font-size: 26px;
  }
  .popup__subtitle {
    font-size: 16px;
  }
}

@media screen and (max-width: 350px) {
  .popup__title {
    font-size: 22px;
  }
  .popup__subtitle {
    font-size: 14px;
  }
}
