.button {
  display: inline-block;
  cursor: pointer;
  text-align: center;
  color: #ffc670;
  border-radius: 40px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  outline: none;
  font-size: 24px;
  background-color: transparent;
  border: none;
  width: 100%;
}

.button_transparent, .button_filled {
  border: 1px solid #ffc670;
  transition: all 0.3s ease-out 0s;
  padding: 15px 20px;
}

.button_transparent:hover {
  background-color: #ffc670;
  color: #161616;
}

.button_filled {
  background-color: #ffc670;
  color: #161616;
}

.button_filled:hover {
  background-color: transparent;
  color: #ffc670;
}

@media screen and (max-width: 900px) {
  .button {
    font-size: 20px;
  }
}

@media screen and (max-width: 560px) {
  .button {
    font-size: 18px;
  }
  .button_transparent, .button_filled {
    padding: 14px 20px;
  }
}
