$body_bg: #161616;
$container_bg: #262626;
$text_color: #ffffff;
$text_color_black: #161616;
$yellow: #ffc670;
$err_red: #fc6666;

$transition_duration: .3s;

@mixin ffMontserrat($weight) {
    font-family: 'Montserrat', sans-serif;
    font-weight: $weight; }

@mixin transition($prop) {
    transition: $prop $transition_duration ease-out 0s; }
