@import "./variables";

.section-title {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    overflow: hidden;
    width: calc( 100% - 30px );
    margin: {
        left: 15px;
        right: 15px; }
    &__text, &__subtext {
        display: inline-block;
        text-align: center;
        position: relative;
        @include ffMontserrat(300);
        color: $text_color; }
    &__text + &__subtext {
        margin-top: 10px; }
    &__subtext {
        font-size: 18px; }
    &__text {
        font-size: 36px;
        &::before, &::after {
            content: '';
            display: inline-block;
            position: absolute;
            top: 50%;
            height: 1px;
            width: 200px;
            background-color: $yellow; }
        &::before {
            left: -20px;
            transform: translate(-100%, -50%); }
        &::after {
            right: -20px;
            transform: translate(100%, -50%); } } }

@media screen and ( max-width: 900px ) {
    .section-title {
        &__text {
            font-size: 28px; } } }

