@import "./variables";

.form {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    & > * + * {
        margin-top: 10px; }
    &__label {
        border-radius: 40px;
        overflow: hidden;
        background-color: #595959;
        position: relative;
        &::before {
            content: '';
            display: block;
            width: 120%;
            height: 120%;
            background-color: $yellow;
            position: absolute;
            pointer-events: none;
            left: 0;
            top: 0;
            transform: translate(-80%, -30%);
            opacity: 0;
            @include transition(all);
            transition-duration: .5s;
            border-radius: 40px; }
        &_focused {
            &::before {
                opacity: 1;
                transform: translate(0%, 0%); } }
        &_invalid {
            &::before {
                background-color: $err_red;
                opacity: 1;
                transform: translate(0%, 0%); } } }
    &__input {
        position: relative;
        z-index: 2;
        border: none;
        display: block;
        width: calc( 100% - 2px );
        @include ffMontserrat(300);
        font-size: 18px;
        padding: 20px 30px;
        background-color: #2c2b2b;
        outline: none;
        margin: 1px;
        color: $text_color;
        border-radius: 40px;
        overflow: hidden; }
    .button {
        padding: 15.5px 20px; } }

@media screen and ( max-width: 420px ) {
    .form {
        &__input, .button {
            padding: 16px 26px;
            font-size: 16px; } } }
