@import "./variables";

.button {
    display: inline-block;
    cursor: pointer;
    text-align: center;
    color: $yellow;
    border-radius: 40px;
    @include ffMontserrat(300);
    outline: none;
    font-size: 24px;
    background-color: transparent;
    border: none;
    width: 100%;
    &_transparent, &_filled {
        border: 1px solid $yellow;
        @include transition(all);
        padding: 15px 20px; }
    &_transparent {
        &:hover {
            background-color: $yellow;
            color: $text_color_black; } }
    &_filled {
        background-color: $yellow;
        color: $text_color_black;
        &:hover {
            background-color: transparent;
            color: $yellow; } } }

@media screen and ( max-width: 900px ) {
    .button {
        font-size: 20px; } }

@media screen and ( max-width: 560px ) {
    .button {
        font-size: 18px;
        &_transparent, &_filled {
            padding: 14px 20px; } } }
