@import "./variables";

.questions {
    display: flex;
    align-items: center;
    padding: 0px 115px 110px;
    &__text + &__form {
        margin-left: 20px; }
    &__text {
        flex-grow: 1; }
    &__title, &__subtitle {
        color: $text_color;
        @include ffMontserrat(300); }
    &__title {
        font-size: 48px;
        margin-bottom: 10px; }
    &__subtitle {
        font-size: 24px; }
    &__form {
        flex-shrink: 0;
        width: 100%;
        max-width: 290px; } }

@media screen and ( max-width: 970px ) {
    .questions {
        padding: 0px 20px 80px; } }

@media screen and ( max-width: 830px ) {
    .questions {
        flex-direction: column;
        align-items: flex-start;
        &__text + &__form {
            margin-left: 0px;
            margin-top: 30px; }
        &__form {
            max-width: 340px; }
        &__title {
            font-size: 36px; }
        &__subtitle {
            font-size: 20px; } } }

@media screen and ( max-width: 560px ) {
    .questions {
        padding: 0px 0px 80px; } }

@media screen and ( max-width: 420px ) {
    .questions {
        &__title {
            font-size: 32px; }
        &__subtitle {
            font-size: 18px; } } }

