@import "./variables";

.contact {
    background-repeat: no-repeat;
    background-size: 30px auto;
    padding-left: 50px;
    background-position: 0 0;
    position: relative;
    &__text {
        display: block;
        width: 100%;
        font-size: 24px;
        @include ffMontserrat(300);
        color: $text_color;
        line-height: 130%;
        & + & {
            margin-top: 5px; }
        &_nowrap {
            white-space: nowrap; }
        &_small {
            font-size: 18px; } }
    &__button {
        position: absolute;
        height: 100%;
        width: 100%;
        left: 0;
        top: 0; }
    &_map {
        background-image: url(../img/map-icon.png); }
    &_phone {
        background-image: url(../img/phone-icon.png); }
    &_mail {
        background-image: url(../img/mail-icon.png); } }

@media screen and ( max-width: 900px ) {
    .contact {
        padding-left: 35px;
        background-size: 25px auto;
        background-position: 0 5px;
        &__text {
            font-size: 18px;
            &_small {
                font-size: 16px; } } } }
