.individual-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 260px;
}

.individual-item__img {
  height: 50px;
  width: auto;
  object-fit: contain;
  margin-bottom: 22px;
}

.individual-item__text {
  color: #ffffff;
  text-align: center;
  display: inline-block;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  font-size: 18px;
}

@media screen and (max-width: 930px) {
  .individual-item__text {
    font-size: 16px;
  }
}
