.questions {
  display: flex;
  align-items: center;
  padding: 0px 115px 110px;
}

.questions__text + .questions__form {
  margin-left: 20px;
}

.questions__text {
  flex-grow: 1;
}

.questions__title, .questions__subtitle {
  color: #ffffff;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
}

.questions__title {
  font-size: 48px;
  margin-bottom: 10px;
}

.questions__subtitle {
  font-size: 24px;
}

.questions__form {
  flex-shrink: 0;
  width: 100%;
  max-width: 290px;
}

@media screen and (max-width: 970px) {
  .questions {
    padding: 0px 20px 80px;
  }
}

@media screen and (max-width: 830px) {
  .questions {
    flex-direction: column;
    align-items: flex-start;
  }
  .questions__text + .questions__form {
    margin-left: 0px;
    margin-top: 30px;
  }
  .questions__form {
    max-width: 340px;
  }
  .questions__title {
    font-size: 36px;
  }
  .questions__subtitle {
    font-size: 20px;
  }
}

@media screen and (max-width: 560px) {
  .questions {
    padding: 0px 0px 80px;
  }
}

@media screen and (max-width: 420px) {
  .questions__title {
    font-size: 32px;
  }
  .questions__subtitle {
    font-size: 18px;
  }
}
