.container {
  position: relative;
  z-index: 2;
  margin: 0 auto;
  width: 100%;
  max-width: 1220px;
  padding: 40px 30px 65px;
  background-color: #262626;
  border-radius: 10px 10px 0px 0px;
  background-size: cover;
  background-image: url(../img/container-bg.jpg);
  overflow: hidden;
}

@media screen and (max-width: 720px) {
  .container {
    padding: 30px 15px 45px;
  }
}
