@import "./variables";

.hero {
    padding: 21% 0px 27% 95px;
    position: relative;
    &__tv {
        top: -130px;
        right: -130px;
        z-index: -1;
        position: absolute; }
    &__title {
        font-size: 60px;
        color: $text_color;
        margin-bottom: 45px;
        @include ffMontserrat(300); }
    &__button {
        width: 100%;
        max-width: 300px; } }

@media screen and ( max-width: 1090px ) {
    .hero {
        &__tv {
            top: -70px;
            right: -150px; } } }

@media screen and ( max-width: 900px ) {
    .hero {
        padding: 15% 0px 17%;
        &__title {
            font-size: 38px; } } }

@media screen and ( max-width: 720px ) {
    .hero {
        padding: 17% 0px;
        &__title {
            margin-bottom: 25px; }
        &__tv {
            top: -40px;
            right: -120px; } } }

@media screen and ( max-width: 560px ) {
    .hero {
        display: flex;
        flex-direction: column-reverse;
        padding: 57% 0px 17%;
        &__tv {
            top: -50px;
            right: 50%;
            transform: translateX(50%); } } }

@media screen and ( max-width: 450px ) {
    .hero {
        padding: 57% 0px 17%;
        &__title {
            font-size: 32px; }
        &__button {
            max-width: none; }
        &__tv {
            max-width: none;
            width: 110%;
            top: -50px; } } }

@media screen and ( max-width: 350px ) {
    .hero {
        &__tv {
            top: -30px; } } }


