@import "./variables";

.individual-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 260px;
    &__img {
        height: 50px;
        width: auto;
        object-fit: contain;
        margin-bottom: 22px; }
    &__text {
        color: $text_color;
        text-align: center;
        display: inline-block;
        @include ffMontserrat(300);
        font-size: 18px; } }

@media screen and ( max-width: 930px ) {
    .individual-item {
        &__text {
            font-size: 16px; } } }
