@import "./variables";

.popup {
    position: fixed;
    z-index: 10;
    background-color: rgba(0, 0, 0, .4);
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    @include transition(all);
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
    &__body {
        position: relative;
        background-color: $container_bg;
        box-shadow: 0 0 10px 4px rgba(0, 0, 0, .6);
        padding: 60px 20px 50px;
        border-radius: 10px;
        overflow: hidden;
        width: 100%;
        max-width: 500px;
        @include transition(all); }
    &__close {
        position: absolute;
        right: 10px;
        top: 10px;
        height: 40px;
        width: 40px;
        &::before, &::after {
            content: '';
            display: inline-block;
            height: 2px;
            width: 25px;
            background-color: #fff;
            position: absolute;
            top: 50%;
            left: 50%; }
        &::before {
            transform: translate(-50%, -50%) rotate(-45deg); }
        &::after {
            transform: translate(-50%, -50%) rotate(45deg); } }
    &__title, &__subtitle {
        @include ffMontserrat(300);
        color: $text_color;
        text-align: center;
        display: block; }
    &__title {
        font-size: 32px;
        margin-bottom: 25px; }
    &__subtitle {
        font-size: 20px; }
    &__title + &__subtitle {
        margin-top: -10px;
        margin-bottom: 45px; }
    &__contact {
        margin-bottom: 10px; }
    &__form {
        & > * + * {
            margin-top: 20px; } }
    &__return {
        display: block;
        width: 100%;
        max-width: 350px;
        margin: 0 auto; }
    &:not(.active) {
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
        .popup__body {
            transform: translateY(-40px);
            opacity: 0; } } }

@media screen and ( max-width: 500px ) {
    .popup {
        &__title {
            font-size: 26px; }
        &__subtitle {
            font-size: 16px; } } }

@media screen and ( max-width: 350px ) {
    .popup {
        &__title {
            font-size: 22px; }
        &__subtitle {
            font-size: 14px; } } }
