.section-title {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  overflow: hidden;
  width: calc( 100% - 30px);
  margin-left: 15px;
  margin-right: 15px;
}

.section-title__text, .section-title__subtext {
  display: inline-block;
  text-align: center;
  position: relative;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  color: #ffffff;
}

.section-title__text + .section-title__subtext {
  margin-top: 10px;
}

.section-title__subtext {
  font-size: 18px;
}

.section-title__text {
  font-size: 36px;
}

.section-title__text::before, .section-title__text::after {
  content: '';
  display: inline-block;
  position: absolute;
  top: 50%;
  height: 1px;
  width: 200px;
  background-color: #ffc670;
}

.section-title__text::before {
  left: -20px;
  transform: translate(-100%, -50%);
}

.section-title__text::after {
  right: -20px;
  transform: translate(100%, -50%);
}

@media screen and (max-width: 900px) {
  .section-title__text {
    font-size: 28px;
  }
}
