@import "./variables";

.individual {
    overflow: hidden;
    padding: 0px 30px 105px 50px;
    &__title {
        margin-bottom: 42px; }
    &__row {
        display: flex;
        align-items: stretch;
        justify-content: space-between; }
    &__img {
        flex-grow: 1;
        display: block;
        position: relative;
        img {
            position: absolute;
            top: 50%;
            left: 53%;
            transform: translate(-50%, -50%);
            object-fit: cover;
            width: 180%;
            max-width: none !important; } }
    &__list {
        flex-shrink: 0;
        display: grid;
        grid-template-columns: 1fr;
        row-gap: 37px;
        position: relative;
        z-index: 2; } }

@media screen and ( max-width: 1080px ) {
    .individual {
        padding: 0px 0px 80px; } }

@media screen and ( max-width: 930px ) {
    .individual {
        &__row {
            align-items: center;
            flex-direction: column; }
        &__list {
            grid-template-columns: repeat(3, 1fr);
            column-gap: 20px;
            justify-items: center;
            width: 100%; }
        &__img {
            min-height: 300px;
            width: 100%;
            img {
                width: 120%;
                left: 50%; } } } }

@media screen and ( max-width: 600px ) {
    .individual {
        &__list {
            display: flex;
            align-items: baseline;
            justify-content: space-around;
            flex-wrap: wrap; }
        &__img {
            img {
                width: 140%; } } } }

@media screen and ( max-width: 500px ) {
    .individual {
        &__img {
            img {
                width: 180%; } } } }
