@import "./variables";

.technology {
    background-color: #161616;
    display: flex;
    align-items: flex-end;
    padding: 15px 20px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    &__name {
        @include ffMontserrat(300);
        font-size: 18px;
        display: inline-block;
        position: relative;
        color: $text_color;
        padding-bottom: 10px;
        &::after {
           content: '';
           display: inline-block;
           position: absolute;
           left: 0;
           bottom: 0;
           height: 1px;
           width: 100%;
           background-color: $yellow;
           max-width: 200px; } } }

@media screen and ( max-width: 610px ) {
    .technology {
        min-height: 280px; } }

@media screen and ( max-width: 370px ) {
    .technology {
        min-height: 240px; } }
