@import "./variables";

.logo {
    display: block;
    flex-shrink: 0;
    width: 100%;
    max-width: 245px;
    &__img {
        width: 100%;
        height: auto; } }

@media screen and ( max-width: 900px ) {
    .logo {
        max-width: 200px; } }

@media screen and ( max-width: 560px ) {
    .logo {
        max-width: 180px; } }
