.style-item {
  width: 100%;
  position: relative;
  padding-bottom: 100%;
  background-color: #000;
  display: block;
  background-position: left top;
  background-repeat: no-repeat;
  background-size: cover;
}

.style-item::before {
  max-width: 220px;
  width: 100%;
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  pointer-events: none;
  background-image: url(../img/style-deco.png);
  background-repeat: no-repeat;
  background-size: cover;
}

.style-item__name {
  position: absolute;
  left: 20px;
  right: 20px;
  bottom: 30px;
  color: #ffffff;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  font-size: 33px;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 15px;
  border-bottom: 1px solid #ffc670;
  z-index: 2;
}

.style-item__name::before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}
