.form {
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.form > * + * {
  margin-top: 10px;
}

.form__label {
  border-radius: 40px;
  overflow: hidden;
  background-color: #595959;
  position: relative;
}

.form__label::before {
  content: '';
  display: block;
  width: 120%;
  height: 120%;
  background-color: #ffc670;
  position: absolute;
  pointer-events: none;
  left: 0;
  top: 0;
  transform: translate(-80%, -30%);
  opacity: 0;
  transition: all 0.3s ease-out 0s;
  transition-duration: .5s;
  border-radius: 40px;
}

.form__label_focused::before {
  opacity: 1;
  transform: translate(0%, 0%);
}

.form__label_invalid::before {
  background-color: #fc6666;
  opacity: 1;
  transform: translate(0%, 0%);
}

.form__input {
  position: relative;
  z-index: 2;
  border: none;
  display: block;
  width: calc( 100% - 2px);
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  font-size: 18px;
  padding: 20px 30px;
  background-color: #2c2b2b;
  outline: none;
  margin: 1px;
  color: #ffffff;
  border-radius: 40px;
  overflow: hidden;
}

.form .button {
  padding: 15.5px 20px;
}

@media screen and (max-width: 420px) {
  .form__input, .form .button {
    padding: 16px 26px;
    font-size: 16px;
  }
}
