.technology {
  background-color: #161616;
  display: flex;
  align-items: flex-end;
  padding: 15px 20px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.technology__name {
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  font-size: 18px;
  display: inline-block;
  position: relative;
  color: #ffffff;
  padding-bottom: 10px;
}

.technology__name::after {
  content: '';
  display: inline-block;
  position: absolute;
  left: 0;
  bottom: 0;
  height: 1px;
  width: 100%;
  background-color: #ffc670;
  max-width: 200px;
}

@media screen and (max-width: 610px) {
  .technology {
    min-height: 280px;
  }
}

@media screen and (max-width: 370px) {
  .technology {
    min-height: 240px;
  }
}
